import io from 'socket.io-client';

// import { CONFIG } from 'core/config';

export class Socket {
  private io: SocketIOClient.Socket;

  constructor() {
    debugger;
    this.io = io.connect('http://localhost:8081/', {
      transports: ['websocket'],
      forceNew: true,
      port: '8081',
    });
    this.io.on('connect', () => console.info('Socket connected'));
  }
}
